body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.userNav {
  background-color: lightgrey;
  border-bottom: 1px solid lightslategrey;
}
.repoName {
  color: #0000cd;
  display: flex;
  flex-direction: row;
  margin-left: 2%;
}

.userName {
  margin-top: 32px;
  font-size: 20px;
}

.header-subNav {
  display: flex;
  flex-direction: row;
  width: 89.9%;
  margin-left: 5%;
  /* height: 60px; */
  background-color: lightgray;
  padding-left: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
}
/* 
.header-subNav img {
  width: 10px;
  height: 10px;
} */

.icon {
  width: 17px;
  height: 17px;
  margin-right: 6px;
  margin-top: 4px;
  border-radius: 50%;
}

.header {
  display: flex;
  flex-direction: row;
  width: 89.9%;
  margin-left: 5%;
  /* height: 60px; */
  background-color: lightgray;
  padding-left: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid lightslategrey;
}

.header img {
  width: 10px;
  height: 10px;
}

.header-sub {
  width: 10%;
}

.container {
  margin: 0 auto;
  background-color: white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.5;
}
.containers {
  display: flex;
  flex-flow: row wrap;
  /* gap: 10px 10px; */
}
.issue {
  display: inline-block;
  margin-top: -6px;
  font-size: 10px;
  border-radius: 2em;
  margin-left: 5%;
  width: 10%;
  margin-bottom: -6px;
}

.card {
  border: 1px solid lightslategrey;
  color: black;
  width: 91.85%;
  margin-left: 5%;
}

.image {
  width: 40px;
  height: 40px;
}
.card h3 {
  width: 80%;
  margin-left: 1%;
}
.container-right {
  display: none;
  margin-top: -35px;
}

.title {
  /* display: inline; */
  display: flex;
  flex-direction: row;
  width: 50%;
}

.openedBy {
  margin-left: 1%;
}

.statusDivMain {
  display: flex;
  flex-direction: row;
}
.statusDiv {
  display: flex;
  flex-direction: row;
  margin-left: 5%;
}

.exclaim {
  height: 20px;
  margin-top: 25px;
  width: 20px;
  border-radius: 50%;
  margin-right: 6px;
}
@media (min-width: 600px) {
  .title {
    display: inline;
    width: 70%;
  }
  .issue-container {
    display: inline;
    flex-flow: row wrap;
    gap: 0;
  }
  .container-right {
    display: flex;
    width: 40px;
    align-items: center;
    margin-left: 96%;
  }
}
